<template>
	<gracePage :customHeader="false">
		<view slot="gBody" class="grace-body">
			<view class="grace-form grace-margin-top">
				<view class="grace-items">
					<text class="dotnet-icons icon-wms-user-c icon grace-blue"></text>
					<view class="grace-label">姓名</view>
					<input type="text" class="input text-align-left" name="userName" v-model="userName" placeholder="请输入您的真实姓名(必填)" />
					<view class="grace-form-funs grace-icons icon-close" @tap="userName = ''" v-if="userName.length > 0"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-tel icon grace-blue"></text>
					<view class="grace-label">手机</view>
					<input type="number" class="input text-align-left" name="mobile" v-model="mobile" placeholder="请输入手机号码(必填)" />
					<view class="grace-form-funs grace-icons icon-close" @tap="mobile = ''" v-if="mobile.length > 0"></view>
				</view>

				<view class="grace-items">
					<text class="dotnet-icons icon-wms-key icon grace-blue"></text>
					<view class="grace-label">密码</view>
					<input :type="showPassword ? 'text' : 'password'" class="input text-align-left" name="password" v-model="password" placeholder="请输入您的登录密码" />
					<view class="grace-form-funs grace-icons icon-eye" :style="{ color: showPassword ? '#007aff' : '' }" @tap="showPassword = !showPassword"></view>
				</view>

				<view class="grace-items">
					<text class="dotnet-icons icon-wms-key icon grace-blue"></text>
					<view class="grace-label">密码</view>
					<input
						:type="showPassword ? 'text' : 'password'"
						class="input text-align-left"
						name="confirmPassword"
						v-model="confirmPassword"
						placeholder="请再次输入您的登录密码"
					/>
					<view class="grace-form-funs grace-icons icon-eye" :style="{ color: showPassword ? '#007aff' : '' }" @tap="showPassword = !showPassword"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-home grace-light-blue"></text>
					<text class="margin-right-ten"></text>
					<view class="grace-label ">公司</view>
					<input class="input text-align-left" name="companyName" v-model="companyName" placeholder="请输入公司名称(必填)" />
					<view class="grace-form-funs grace-icons icon-close" @tap="companyName = ''" v-if="companyName.length > 0"></view>
				</view>

				<!-- <view class="grace-form-item grace-border-b" @tap="open2"> -->
				<view class="grace-items" @tap="open2">
					<text class="grace-icons icon-address grace-light-blue"></text>
					<text class="margin-right-ten"></text>
					<text class="grace-form-label">地区</text>
					<view class="grace-form-body"><input type="text" class="grace-form-input" :value="value2" disabled placeholder="请选择" /></view>
					<text class="grace-icons icon-arrow-down grace-form-icon grace-text-center"></text>
				</view>
				<graceAddressPicker :show="show2" @confirm="confirm2" @close="close2" :value="default2"></graceAddressPicker>

				<view class="grace-items">
					<text class="grace-icons icon-face grace-light-blue"></text>
					<text class="margin-right-ten"></text>
					<view class="grace-label">职务</view>
					<input class="input text-align-left" name="jobTitle" v-model="jobTitle" placeholder="请输入职务" />
					<view class="grace-form-funs grace-icons icon-close" @tap="jobTitle = ''" v-if="jobTitle.length > 0"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-address grace-blue"></text>
					<text class="margin-right-ten"></text>
					<view class="grace-label">邮箱</view>
					<input type="text" class="input text-align-left" name="email" v-model="email" placeholder="请输入邮箱" />
					<view class="grace-form-funs grace-icons icon-close" @tap="email = ''" v-if="email.length > 0"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-tel grace-blue"></text>
					<text class="margin-right-ten"></text>
					<view class="grace-label">电话</view>
					<input type="text" class="input text-align-left" name="telephone" v-model="telephone" placeholder="请输入电话号码" />
					<view class="grace-form-funs grace-icons icon-close" @tap="telephone = ''" v-if="telephone.length > 0"></view>
				</view>

				<view><textarea placeholder="注册说明..." name="description" v-model="description" class="description" /></view>

				<view @tap="applyPost"><button class="btn-submit" type="primary">立即注册</button></view>
			</view>

			<graceHeaderAlert :show="showErrorMessage" background="#FF0036">
				<text class="grace-h5 grace-white">{{ errorMessage }}</text>
			</graceHeaderAlert>

			<graceHeaderAlert :show="showSucessMessage" background="#9DD26D">
				<text class="grace-icons icon-right grace-white grace-h5" style="margin-right:20rpx;"></text>
				<text class="grace-h5 grace-white">注册成功，请登录</text>
			</graceHeaderAlert>
		</view>
	</gracePage>
</template>

<script>
import graceAddressPicker from '../../graceUI/components/graceAddressPicker.vue';
export default {
	data() {
		return {
			userName: '',
			mobile: '',
			password: '',
			confirmPassword: '',
			companyName: '',
			jobTitle: '',
			email: '',
			telephone: '',
			description: '',
			positionTop: 0,
			showPassword: false,
			show2: false,
			value2: '',
			province: '',
			city: '',
			district: '',
			// 第2个级联的默认值
			// ["26", "0", "5"] 代表 "陕西省", "西安市", "雁塔区"
			default2: [8, 0, 5],
			showErrorMessage: false,
			errorMessage: '',
			showSucessMessage: false
		};
	},
	onLoad() {
		var me = this;
		var userInfo = me.getCurrentUserInfo();
		if (userInfo !== null) {
			//未登录，返回登陆界面
			uni.reLaunch({
				url: 'usercenter'
			});
		}
	},
	components: {
		graceAddressPicker
	},
	methods: {
		initPosition() {
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		},

		applyPost() {
			var me = this;
			if (me.userName == '') {
				me.errorMessage = '请输入申请人姓名';
				me.showErrorMessage = !me.showErrorMessage;
				return;
			}
			if (!this.checkMobile(this.mobile)) {
				me.errorMessage = '请输入正确手机号码';
				me.showErrorMessage = !me.showErrorMessage;
				return;
			}
			if (this.companyName == '') {
				me.errorMessage = '请输入公司名称';
				me.showErrorMessage = !me.showErrorMessage;
				return;
			}
			// if (!this.checkEmail(this.email)) {
			// 	uni.showToast({
			// 		icon: 'none',
			// 		title: '请输入正确邮箱地址'
			// 	});
			// 	return;
			// }
			if (this.password.length < 6) {
				me.errorMessage = '密码最短为 6 个字符';
				me.showErrorMessage = !me.showErrorMessage;
				return;
			}
			if (this.password != this.confirmPassword) {
				me.errorMessage = '两次输入密码不一致';
				me.showErrorMessage = !me.showErrorMessage;
				return;
			}
			var plantForm = '';
			uni.getSystemInfo({
				success: (res) => {
					// console.log(res.platform);
					if (res.platform == "android") {
						plantForm = 'android'
					} else if (res.platform == "ios") {
						plantForm = 'ios'
					} else {
						plantForm = 'miniprogram'
					}
				}
			})
			uni.showLoading({
				title: '注册中...'
			});
			var me = this;
			var serverUrl = me.getServerUrl();
			// console.log(serverUrl);
			uni.request({
				url:
					serverUrl +
					'/User/Register?mobile=' +
					me.mobile +
					'&companyName=' +
					me.companyName +
					'&password=' +
					me.password +
					'&confirmPassword=' +
					me.confirmPassword +
					'&email=' +
					me.email +
					'&realName=' +
					me.userName +
					'&province=' +
					me.province +
					'&city=' +
					me.city +
					'&district=' +
					me.district +
					'&title=' +
					me.jobTitle +
					'&telephone=' +
					me.telephone +
					'&description=' +
					me.description + 
					'&userFrom=' +
					me.plantForm,
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret
				},
				success: res => {
					if (res.data.toString().indexOf('Error') != -1) {
						uni.showToast({
							title: me.serverUrlName + '维护错误'
						});
						setTimeout(function() {
							uni.reLaunch({
								url: 'login'
							});
						}, 1000);
					} else {
						if (res.data.Status) {
							me.showSucessMessage = !me.showSucessMessage;
							
							//直接登录
							uni.request({
								url: serverUrl + '/User/Login?userName=' + me.mobile + '&password=' + me.password,
								method: "POST",
								header: {
									"appKey": me.appKey,
									"appSecret": me.appSecret
								},
								success: (res) => {
									if (res.data.toString().indexOf('Error') != -1) {
										uni.showToast({
											title: me.serverUrlName + '维护错误'
										})
										setTimeout(function() {
											uni.reLaunch({
												url: 'updateserverurl?returnurl=login'
											})
										}, 1000)
									} else {
										if (res.data.Status) {
											//getUserContact();
											var userInfo = res.data.Data;
											//向userInfo添加过期日期
											//var cookieHours = me.getCookieHours();
											var cookieHours = 168;
											var timeNow = new Date();
											var expiredDate = timeNow.setTime(timeNow.setHours(timeNow.getHours() + cookieHours));
											userInfo.expiredDate = expiredDate;
											// 保存用户信息到全局的缓存中
											uni.setStorageSync("currentUserInfo", userInfo);
											//console.log(userInfo);
											//保存serverUrl至storage
											//debugger;
											// 切换页面跳转，使用tab切换的api
											uni.switchTab({
												url: "../main/main"
											});
										}
									}
								}
							});
							//登录结束

							// setTimeout(function() {
							// 	uni.reLaunch({
							// 		url: 'login'
							// 	});
							// }, 2000);
						} else {
							me.errorMessage = res.data.StatusMessage;
							me.showErrorMessage = !me.showErrorMessage;
							return;
						}
					}
				},
				fail: function() {
					uni.showToast({
						title: '网络异常请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					setTimeout(function() {
						uni.hideLoading();
					}, 2000);
				}
			});
		},

		open2: function() {
			this.show2 = true;
		},
		confirm2: function(e) {
			// 返回选中城市的编号及文本信息
			console.log(e);
			this.value2 = e.names[0] + ',' + e.names[1] + ',' + e.names[2];
			(this.province = e.names[0]), (this.city = e.names[1]), (this.district = e.names[2]), this.close2();
		},
		close2: function() {
			this.show2 = false;
		},
		checkMobile: function(mobile) {
			return RegExp(/^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/).test(mobile);
		},
		checkEmail: function(email) {
			return RegExp(/^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/).test(email);
		},
		onNavigationBarButtonTap(e) {
			if (e.type == 'back') {
				uni.reLaunch({
					url: 'login'
				});
			}
		}
	},
	onReady() {
		this.initPosition();
	}
};
</script>

<style></style>
